@import url(https://fonts.googleapis.com/css2?family=Orbitron:wght@500;700&display=swap);


:root {
  --button-op-color: white;
  --text-color: #fff;
  --button-color: rgb(54, 52, 52);
  --bg-color: rgb(230, 188, 117);
  --text-color-dark: black;
  --button-color-dark: gray;
}

.dark {
  --button-color: rgb(248, 248, 248);
  --text-color: rgb(54, 52, 52);
  --text-color-dark: white;
  --button-op-color: orange;
  --bg-color: rgb(223, 200, 160);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
body {
  background-color: rgb(125, 205, 211);
  display: grid;
  place-content: center;
  font-family: "Orbitron", sans-serif;
  height: 100vh;
  font-size: 2rem;
}

.container {
  padding: 0px;
  height: 700px;
  width: 400px;
  overflow: hidden;
  border-radius: 20px;
  background-color: rgb(230, 188, 117);
  background-color: var(--bg-color);
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;

  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.wrapper {
  display: grid;
  height: 100%;
  width: 100%;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 1fr);
  grid-gap: 0px;
  gap: 0px;
}

.screen {
  grid-column: 1 / -1;
  color: #fff;
  color: var(--text-color);
  text-align: right;
  padding: 0.5rem;
  font-size: 4rem;
  overflow: hidden;
}

.btn {
  border: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  border-right: 1px solid rgba(0, 0, 0, 0.05);
  display: grid;
  place-content: center;
  background-color: rgb(54, 52, 52);
  background-color: var(--button-color);

  color: #fff;

  color: var(--text-color);
  cursor: pointer;
  font-weight: bold;
}
.btn:hover {
  background-color: gray;
}

.gray {
  background-color: gray;
  background-color: var(--button-color-dark);
  color: black;
  color: var(--text-color-dark);
}

.gray:hover {
  background-color: white;
  color: orange;
}

.operator {
  background-color: rgb(64, 105, 182);
  color: white;
  color: var(--button-op-color);
}

.operator:hover {
  background-color: white;
  color: orange;
}
.eval {
  background-image: linear-gradient(312deg, #5b0766, #3097f7);
  color: white;
}
.eval:hover {
  background-image: linear-gradient(312deg, #5b0766, #3097f7);

  color: orange;
}

